import React from 'react'
import { Link } from 'react-router-dom'
import Gender from './gender'
import Ghostel from './Ghostel';
import Bhostel from './Bhostel';
import Confirmation from '../Confirmation';

import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { reactLocalStorage } from 'reactjs-localstorage';

export default function roomallot() {
    const Userdata = JSON.parse(reactLocalStorage.get('userdata'))
    const userid = Userdata.email.substring(0, Userdata.email.indexOf('@'));
    function changereq()
    {
        reactLocalStorage.remove(userid);

    }
 
    return (
        reactLocalStorage.get(userid)===undefined ?
        <>
            <div className="Header">
                <h3>Room Alloter</h3>
                <div>
                    <Link to="/dashboard" id="backbtn">BACK</Link>
                </div>
            </div>
            

             <div className="bodypart" id="bodypart">
                <Router>
                    <Switch>
                        <Route exact path="/dashboard/roomallot/" component={Gender} />
                        <Route path="/dashboard/roomallot/girlshostel" component={Ghostel}/>
                        <Route path="/dashboard/roomallot/boyshostel" component={Bhostel}/>
                        <Route path="/dashboard/roomallot/Booked" component={Confirmation}/>
                    </Switch>
                </Router>
            </div>
        </>
        : 
        <>
        <div className="Header">
            <h3>Room Alloter</h3>
            <div>
                <Link to="/dashboard" id="backbtn">BACK</Link>
            </div>
        </div>

        

         <div className="bodypart" id="bodypart">
            <h2>YOUR ROOM IS BOOKED!!</h2>
            <h4>ROOM DETAILS:-</h4>
            <h2>{reactLocalStorage.get(userid)}</h2>
            <Link to="/dashboard/roomallot" id="CHNG" onClick={()=>changereq()}>Change Room</Link>
        </div>
    </>
    )
}
