import React from 'react'
import { Link } from 'react-router-dom'

export default function about() {
    return (
        <>
            <div className="Header">

                <h3>About Us</h3>
                <div>
                    <Link to="/dashboard" id="backbtn">BACK</Link>
                </div>
            </div>
            <div className="bodypart">
                <p className="para">
                    It is a hostel management application developed on the platform
                    of React Js.
                    <br />
                    Features of the application are:-
                    <br />
                    <ul>
                        <li>1. Login Authentication</li>
                        <li>2. User Dashboard Panel</li>
                        <li>3. Hostel Room Booking</li>
                        <li>4. Email Notifiction</li>
                        <li>5. Modern UI</li>
                    </ul>
                    <br />
                    Updates coming soon with more features...
                </p>
            </div>
        </>
    )
}
