import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './style.css'
import home from './DashboardComponents/home'
import about from './DashboardComponents/about'
import gallery from './DashboardComponents/gallery';
import roomallot from './DashboardComponents/roomallot';
import facilities from './DashboardComponents/facilities';
import { reactLocalStorage } from 'reactjs-localstorage';


export default function Dashboard() {
    const history = useHistory()
    const Userdata = JSON.parse(reactLocalStorage.get('userdata'))
    const userid = Userdata.email.substring(0, Userdata.email.indexOf('@'));
    const haveroom = reactLocalStorage.get(userid);

    function handleLogout() {
        history.push("/")
    }



    return (
        <div className="dashbd">
            <div className="Nav">

                <h4><b>HOSTEL MANAGEMENT</b></h4>
                <div>

                    <button id="logoutbtn" onClick={handleLogout}>Log Out</button>
                </div>
            </div>

            <div className="dash">
                <div className="leftpanel">
                    <div className="propic">
                        <img className="userpic" src={Userdata.image} alt={Userdata.name}></img>
                    </div>
                    <h4 id="userid">{Userdata.name}</h4>
                    <div className="id_room">

                        <h6 id="mailidd">{Userdata.email}</h6>
                        <br />
                        <h3>{haveroom}</h3>
                    </div>
                </div>
                <div className="rightpanel">
                    <Router>
                        <Switch>
                            <Route exact path="/dashboard" component={home} />
                            <Route path="/dashboard/about" component={about} />
                            <Route path="/dashboard/gallery" component={gallery} />
                            <Route path="/dashboard/facilities" component={facilities} />
                            <Route path="/dashboard/roomallot" component={roomallot} />
                        </Switch>
                    </Router>
                </div>
            </div>

        </div>
    )
}
