import React from 'react'
import { reactLocalStorage } from 'reactjs-localstorage';
import { Link
 } from 'react-router-dom';
const Confirmation = () => {

    const Userdata = JSON.parse(reactLocalStorage.get('userdata'))
    const usermail = Userdata.email;

    return (
        <div>
            <i class="far fa-check-circle cnfr"></i>
            <h1>ROOM BOOKED</h1>
            <p>An email with room details has been sent to you on mail at "{usermail}".<br/>
                Thank you for using our service.
            </p>
        </div>
    )
}

export default Confirmation
