import React from 'react'
import {Link} from 'react-router-dom'

const Gender = () => {
    return (
        <div className="hostelbox">
            <h3 id="selectgen">Select your gender:-</h3>
            <div id="genders">
            <Link to="/dashboard/roomallot/boyshostel">
                <button id="gen"><i class="fas fa-male"></i></button>
            </Link>
            <Link to="/dashboard/roomallot/girlshostel">
                <button id="gen"><i class="fas fa-female"></i></button>
            </Link>
            </div>
        </div>
    )
}

export default Gender;
