import React, { useState, useEffect } from 'react'
import { reactLocalStorage } from 'reactjs-localstorage';
import { db } from "../../firebase";
import emailjs from 'emailjs-com';
import Popup from './Popup';
import { Link } from 'react-router-dom'

const Bhostel = () => {
    const Userdata = JSON.parse(reactLocalStorage.get('userdata'))
    const [hostel, setHostel] = useState([]);
    const [loading, setLoading] = useState(false);
    const [old, setOld] = useState(undefined);
    const [update, setUpdate] = useState("");
    const [isOpen, setIsOpen] = useState(false);
    const [blk, setBlock] = useState("");
    const [rnm, setRoomnum] = useState("");

    const togglePopup = () => {
        setIsOpen(!isOpen);
    }

    const userid = Userdata.email.substring(0, Userdata.email.indexOf('@'));

    //Send EMail
    function sendMail(block, roomnum) {
        emailjs.send("service_5j3973q", "template_jr1cvgh", {
            userid: userid,
            block: block,
            roomnum: roomnum,
            type: "Boys",
            usermail: Userdata.email,
        }, "user_AekDBT4USZPyqrqsTjMtX")
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
    }

    //Have Old Room?
    useEffect(() => {
        db.collection("StudentDatas").doc("Booked")
            .get()
            .then(function (doc) {
                if (doc.exists) {
                    setOld(doc.data()[userid]);
                } else {
                    // doc.data() will be undefined in this case
                    console.log("No old datas!!");
                }
            }).catch(function (error) {
                console.log("Error getting student old room details:", error);
            });
    }, [update])

    //Get Hostel Datas
    useEffect(() => {
        const getData = async () => {
            setLoading(false);
            db.collection('HostelBlocks').onSnapshot(snapshot => {
                setHostel(snapshot.docs.map(docs => docs.data()));
            });
        }
        getData();

    }, [update]);


    useEffect(() => {
        if (hostel?.length) {
            setLoading(true);
        }
        else
            setLoading(false);


    }, [hostel]);


    //Choosing
    function choosed(block, roomnum) {

        const userroom = "B" + block + ":" + roomnum;
        setBlock(block);
        setRoomnum(roomnum);
        reactLocalStorage.set(userid, userroom);
        db.collection("StudentDatas").doc("Booked").update({ [userid]: { "Hostel": "Boys", "Block": block, "Room": roomnum } });
        hostel[0][block][roomnum - 1] = false;


        //Choose Room
        var ref = db.collection("HostelBlocks").doc("Boys");
        ref.update({
            [block]: hostel[0][block]
        })

        if (old) {
            hostel[0][old.Block][old.Room - 1] = true;
            ref.update({
                [old.Block]: hostel[0][old.Block]
            })
            //console.log("OLD ROOM: " + old.Block + " " + old.Room);
            // console.log("OLD ROOM: " + hostel[0][old.Block]);
        }
        // else
        //     console.log("NO OLD ROOM!!");
        setIsOpen(true);
        setUpdate(userroom);
        sendMail(block, roomnum);
        //alert("NEW ROOM ASSIGNED: " + block + " " + roomnum);

    }

    function showrooms(b_name) {
        if (!loading) {
            document.getElementById("rids").innerText = "Loading Data...";
        }
        else {
            const mainblock = document.getElementById("rids");
            mainblock.innerHTML = "";
            const tot = hostel[0][b_name];

            for (let i = 1; i <= tot.length; i++) {
                const r = document.createElement("button");
                r.innerText = "Room-" + i;
                r.setAttribute("id", tot[i - 1] ? "roomid" : "roomid2");
                r.setAttribute(tot[i - 1] ? "enabled" : "disabled", true);
                r.addEventListener("click", function () {
                    choosed(b_name, i);
                });
                mainblock.appendChild(r);
            }
        }

        //alert("Room Changed!!"+tot.length);
    }
    return (
        <div className="hostelbox">
            <div className="hostel_header">
                <i id="ico" class="fas fa-male"></i>
                <h1>BOY'S HOSTEL!!</h1>
                <i id="ico" class="fas fa-male"></i>
            </div>
            <div className="blocks_col">
                <button id="blkbtn" onClick={() => showrooms("A")}>Block-A</button>
                <button id="blkbtn" onClick={() => showrooms("B")}>Block-B</button>
                <button id="blkbtn" onClick={() => showrooms("C")}>Block-C</button>
                <button id="blkbtn" onClick={() => showrooms("D")}>Block-D</button>
                <button id="blkbtn" onClick={() => showrooms("E")}>Block-E</button>
            </div>
            {
                isOpen && <Popup
                    content={<div class="pbox">
                        <div>
                            <b>Room Booking Details:-</b>
                            <p>Block: {blk}<br />
                                Room No: {rnm}<br />
                                Hostel Type: Boys </p>
                        </div>
                        <div>
                        <Link to="/dashboard/roomallot/Booked" id="cnfrmbtn"><i className="fas fa-check-circle"></i> Confirm</Link>
                        </div>
                    </div>}
                    handleClose={togglePopup}
                />
            }
            <div id="rids">
                Choose Block To See Available Rooms!!

            </div>
        </div>
    )
}

export default Bhostel
