import React, { useRef, useState } from "react"
import { Form, Card, Alert } from 'react-bootstrap'
import { useAuth } from './AuthContext'
import { Link, useHistory } from 'react-router-dom'
import GoogleLogin from "react-google-login"
import { reactLocalStorage } from 'reactjs-localstorage';

export default function Login() {

    const emailRef = useRef();
    const passwordRef = useRef();

    const { login } = useAuth()
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()

    async function handleSubmit(e) {
        e.preventDefault()

        try {
            setError('')
            setLoading(true)
            await login(emailRef.current.value, passwordRef.current.value)
            var str=emailRef.current.value
            var usrname=str.substring(0, str.indexOf('@'))
            reactLocalStorage.set('userdata',
            JSON.stringify({
                "name": usrname,
                "email": emailRef.current.value,
                "image": "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
            }));
            history.push("/dashboard")
        }
        catch {
            setError("Wrong Email/Password!!")
        }
        setLoading(false)
    }


    const responseGoogle = (response) => {
        reactLocalStorage.set('userdata',
            JSON.stringify({
                "name": response.profileObj.name,
                "email": response.profileObj.email,
                "image": response.profileObj.imageUrl
            }));
        history.push("/dashboard")
    }


    return (
        <>
            <div className="appname">
                <i class="fas fa-hotel"></i>
                <h3>HOSTEL MANAGEMENT</h3>
                <p>(Testing Version)</p>
            </div>
            <div className="lsbox">
                <Card.Body className="lsbox">
                    <h2 className="text-center mb-4">Log In</h2>
                    {error && <Alert variant="danger">{error}</Alert>}
                    <Form onSubmit={handleSubmit}>
                        <Form.Group id="email">
                            <Form.Label>Email</Form.Label>
                            <Form.Control type="mail" ref={emailRef} required />
                        </Form.Group>

                        <Form.Group id="password">
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" ref={passwordRef} required />
                        </Form.Group><br />
                        <div className="entrybtns">
                            <button id="logsign" disabled={loading} type="submit">Log In</button>


                            <GoogleLogin
                                clientId="222923456734-s95m9k3u9dtlm87jcbm8e6f1asusvfuq.apps.googleusercontent.com"
                                buttonText="Login"
                                onSuccess={responseGoogle}
                                onFailure={responseGoogle}
                                cookiePolicy={'single_host_origin'}
                            /></div>
                    </Form>
                </Card.Body>
            </div>
            <div className="w-100 text-center mt-2">
                Need an account? <Link to="/signup">Sign Up</Link>
            </div>
        </>
    )
}
